.nav-section {
  color: white;
  font-size: 28px;
  padding: 20px 0px;
  font-family: 'Staatliches', cursive;
}
.game-header {
  background-color: #c0dfd9;
  padding: 20px;
  margin: 20px;
}

.game-board {
  padding-top: 50px;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}
.card {
  background: #fff;
  border-radius: 2px;
  height: 265px;
  margin: 1rem;
  position: relative;
  width: 265px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}

.card > .img-container > img {
  height: 95%;
  margin-top: 2.5%;
}
html, body, #root {
  height: 100%;
  background-color: #e9ece5;
  padding: 0;
  margin: 0;
}

* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
